@font-face {
  font-family: 'icomoon-fa';
  src:  url('fonts/icomoon-fa.eot?ulx4wx');
  src:  url('fonts/icomoon-fa.eot?ulx4wx#iefix') format('embedded-opentype'),
    url('fonts/icomoon-fa.ttf?ulx4wx') format('truetype'),
    url('fonts/icomoon-fa.woff?ulx4wx') format('woff'),
    url('fonts/icomoon-fa.svg?ulx4wx#icomoon-fa') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="fa-"], [class*=" fa-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon-fa' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fa-x:before {
  content: "\e900";
}
.fa-settings:before {
  content: "\e901";
}
.fa-moon:before {
  content: "\e902";
}
.fa-sun:before {
  content: "\e903";
}
