
@import './icomoon-fa/style.css';
@import './normalise.css';

body {
    font-family: 'Noto Sans HK', sans-serif;
	background: var(--background);
}

h1, h2, h3, h4, h5, h6 {
	color: var(--heading) !important;
	margin-bottom: 1em;
	margin-top: 0;
}

p, label, span {
	color: var(--paragraph) !important;
	font-size: 14px;
}
	p.-smaller, label.-smaller, span.-smaller { 
		font-size: 80%;
	}

a {
	color: var(--secondary) !important;
}

.-colour-tertiary {
	color: var(--tertiary) !important;
}

#root {
	position: absolute;
	top: 0; bottom: 0;
	left: 0; right: 0;
	margin: 0; padding: 0;
	display: flex; flex-direction: column; 
	align-items: center;
	overflow: scroll;
}
	.app {
		width: min(70vw, 30em);
		flex-grow: 1;
		display: flex;
	}
		.view-port {
			flex-grow: 1;
			width: min(70vw, 30em);
			position: relative;
		}

		.a-div {
			will-change: transform, opacity;
			display: flex; flex-direction: column; 
			position: absolute;
			top: 0; right: 0; left: 0; bottom: 0;
		}

			.main {
				padding: 2em 0;
				flex-grow: 1;
				display: flex; flex-direction: column; 
				align-items: center; justify-content: space-evenly;
			}

				.main.settings {
					justify-content: center;
				}
		
				.header {
					margin-bottom: 1em;
				}
					.header img {
						width: 85%;
					}

				.timer {
					text-align: center;
					width: 80%;
				}

					.time {
						text-align: center;
						font-size: min(25vw, 7em);
						color: var(--highlight);
					}

					.controls {
						width: 100%; margin: 0 auto;
						margin-top: 2em;
						text-align: center;
					}
						.controls .button {
							display: inline-block;
							padding: 0 0.25em;
							width: min(28%, 8em);
							height: 3em;
						}
							.controls .button button {
								text-align: center;
								border-radius: 8px; border: none;
								height: 100%; width: 100%;
								background: var(--secondary);
								overflow: hidden;
								padding: 0;
								cursor: pointer;
							}

	.toggle {
		position: fixed;
		top: 1rem;
	}
		.toggle button {
			width: 1.5rem; height: 1.5rem;
			background-color: var(--alt-background);
			padding: 0; border: none;
			cursor: pointer; border-radius: 4px;
		}
			.toggle button span::before {
				color: var(--alt-paragraph);
			}
	
	.dark-mode-toggle {
		right: 1rem;
	}

	.settings-toggle {
		right: calc( 1rem + 1.5rem + 1rem );
	}

@media screen and ( max-width: 1100px ) {
    .body {
        padding: 0 1em !important;
	}
        .inputs-box-row {
            justify-content: space-between;
		}
}

@media screen and ( max-width: 600px ) {
	.inputs-box-row > div {
		justify-content: start;
	}
				
	.inputs-box-row > div svg {
		margin-left: 0 !important;
		margin-right: 3em;
	}
}
